import React from "react";
import { NavLink as Link } from 'react-router-dom';

// reactstrap components
// import { Container, Row, Col, Nav, NavItem, NavLink } from "reactstrap";
import {
    Container, Row, Col, Nav, NavItem, NavLink
} from '../../../../components';

// href="#"
// rel="noopener noreferrer"
// target="_blank"

function HomeFooter() {
    return (
      <Container style={{padding:"15px"}}>
      <footer className="footer">
        <Row className="align-items-center justify-content-xl-between">
          <Col xs="3">
            <Row className="justify-content-center">
            <NavLink className="text-muted">
              © 2022{" "}
            </NavLink>
            <NavLink tag={ Link } to="/team">
                    <span style={{color:"#fff"}}>
                    HORAC STUDIO
                    </span>
            </NavLink>
            </Row>
          </Col>

          <Col xl="5">
            <Container className="nav-footer justify-content-center">
                <Row>
                <Nav>
              <NavItem>
                <NavLink tag={ Link } to="/home">
                        <span>
                            Home
                        </span>
                </NavLink>
              </NavItem>

              <NavItem>
                <NavLink tag={ Link } to="/team">
                        <span>
                            Team
                        </span>
                </NavLink>
              </NavItem>

              <NavItem>
                <NavLink tag={ Link } to="/whitePaper">
                        <span>
                        WhitePaper
                        </span>
                </NavLink>
              </NavItem>

              <NavItem>
                <NavLink tag={ Link } to="/promotions">
                        <span>
                        Promotions
                        </span>
                </NavLink>
              </NavItem>
              </Nav>
              </Row>
            </Container>
          </Col>
          <Col>
            <Nav className="nav-footer justify-content-center">
              <NavItem>
                <NavLink
                  href="https://t.me/TheMonsterKingsChannel"
                  rel="The Monster Kings Channel"
                  target="_blank"
                >
                  <i className="fab fa-telegram fa-2x"></i>
                </NavLink>
              </NavItem>

              <NavItem>
                <NavLink
                  href="https://horacstudio.medium.com/"
                  rel="The Monster Kings"
                  target="_blank"
                >
                  <i className="fab fa-medium fa-2x"></i>
                </NavLink>
              </NavItem>

              <NavItem>
                <NavLink
                  href="https://twitter.com/TMorganer"
                  rel="The Monster Kings"
                  target="_blank"
                >
                  <i className="fab fa-twitter fa-2x"></i>
                </NavLink>
              </NavItem>

              <NavItem>
                <NavLink
                  href="https://www.youtube.com/channel/UCyrWJGz9gWVpfPsijXCpMlw"
                  rel="The Monster Kings"
                  target="_blank"
                >
                  <i className="fab fa-youtube fa-2x"></i>
                </NavLink>
              </NavItem>
            </Nav>
          </Col>
        </Row>
      </footer>
      </Container>
    );
}

export default HomeFooter;